import React, {Component} from 'react';
import * as d3 from 'd3';
import './chord.min.css'

class Chord extends Component{
  constructor(){
    super();

    this.ref = null;
    this.node_coords = {};
    this.emoji_width = 16;
    this.emoji_network = {};
    this.state ={
      node: [],
      pairs: []
    }

    this.color = d3.scaleOrdinal()
    .domain(d3.range(8))
    .range(["#f55e65", "#7c004f", "#1a737b", "#01d1c1", "#a8e49c", "#ffb400", "#ffaa91", "#b3a76b"]);
  }

  componentDidMount(){
    this.svg_dim = this.ref.getBoundingClientRect();
    // get the dimensions of the svg
    this.outerRadius = this.svg_dim.width/2 * 0.81;
    this.innerRadius = this.outerRadius - 3;

    d3.queue()
      .defer(d3.csv, './data/airtweets6_hier.csv')
      .defer(d3.csv, './data/airtweets_pairs.csv')
      .await((error, airtweets, pairs) => {
        if (error) throw error;
        let data = processData(airtweets);
        this.setState({node: data, pairs: pairs});
      })
  }

  drawCircles(d){
    let [x,y] = project(d.x, d.y);
    let props = {
      key: d.data.emojis,
      className: 'circleback',
      opacity: 0.8,
      r: Math.sqrt(d.data.n/(Math.PI)) * 2, // so that it corresponds with area of circle but just r is more pronounced, multiplyingwill help amplify a bit?
      cx: x,
      cy: y,
      fill: this.color(d.data.group),
      transform: 'translate(' + this.svg_dim.width/2 +','+ this.svg_dim.height/2 + ')'
    }
    return(<circle {...props}></circle>)

  }

  drawEmojis(d){
    let [x,y] = project(d.x, d.y);
    let emoji_width = this.emoji_width;

    this.node_coords[d.data.emojis] = {x:x, y:y, a:d.x, r:d.y};

    let props = {
      key: d.data.emojis+'img',
      id: d.data.emojis,
      className: 'emojis',
      transform: 'translate(' + (this.svg_dim.width/2-emoji_width/2)+ ',' + (this.svg_dim.height/2-emoji_width/2) + ')',
      x: x,
      y: y,
      outline: 'solid 1px',
      width: emoji_width,
      height: emoji_width,
      href: './emojione/'+ d.data.unicode +  '.svg'
    }
    return(<image {...props} onMouseOver={this.hoverHandler}/>)
  }

  drawLinks(d){
    // store node
    this.emoji_network[d.item1] = this.emoji_network[d.item1] || {'nodes': [], links: []};
    this.emoji_network[d.item1]['nodes'].push(d.item2);

    this.emoji_network[d.item2] = this.emoji_network[d.item2] || {'nodes': [], links: []};
    this.emoji_network[d.item2]['nodes'].push(d.item1);

    // drawlinks
    let emoji_width = this.emoji_width;
    let x1 = (this.node_coords[d.item1].x < this.svg_dim.width/2) ? this.node_coords[d.item1].x + emoji_width : this.node_coords[d.item1].x - emoji_width;
    let y1 = (this.node_coords[d.item1].y < this.svg_dim.height/2) ? this.node_coords[d.item1].y + emoji_width: this.node_coords[d.item1].y - emoji_width;
    let x2 = (this.node_coords[d.item2].x < this.svg_dim.width/2) ? this.node_coords[d.item2].x + emoji_width : this.node_coords[d.item2].x - emoji_width;
    let y2 = (this.node_coords[d.item2].y < this.svg_dim.height/2) ? this.node_coords[d.item2].y + emoji_width: this.node_coords[d.item2].y - emoji_width;

    // let path = 'M ' + x1 + ' ' + y1 + ' L ' + x2 + ' ' + y2;
    let a1 = this.node_coords[d.item1].a;
    let a2 = this.node_coords[d.item2].a;
    let r = this.node_coords[d.item1].r;

    let bez1 = project(a1, r/2);
    let bez2 = project(a2, r/2);
    let path = 'M ' + x1 + ',' + y1 + ' C ' + bez1[0] + ',' + bez1[1] + ' ' + bez2[0] + ',' + bez2[1] + ' ' + x2 + ',' + y2;

    let props = {
      key: d.item1 + '-' + d.item2,
      className: 'line',
      link: d.item1 + '-' + d.item2,
      d: path,
      stroke: 'rgba(156,161,154, 0.3)',
      fill:'none',
      strokeWidth: d.n,
      transform: 'translate(' + (this.svg_dim.width/2-emoji_width)+ ',' + (this.svg_dim.height/2-emoji_width) + ')'
    }

    return(
      <path {...props}/>
    )
  }

  render(){
    let cluster1 = this.ref !== null && d3.cluster().size([360, this.outerRadius]);
    let cluster2 = this.ref !== null && d3.cluster().size([360, this.innerRadius]);
    return(
      <svg ref={(ref)=>{this.ref=ref}} className='chord'>
        {this.state.node.hasOwnProperty('children') &&
          cluster1(this.state.node).leaves().map(d => this.drawCircles(d))}
        {this.state.node.hasOwnProperty('children') &&
           cluster2(this.state.node).leaves().map(d => this.drawEmojis(d))}
        {this.state.pairs.map(d => this.drawLinks(d))}
      </svg>
    )
  }
}

// https://bl.ocks.org/mbostock/4739610f6d96aaad2fb1e78a72b385ab
function project(x, y) {
  let angle = (x - 90) / 180 * Math.PI;
  let radius = y;
  return [radius * Math.cos(angle), radius * Math.sin(angle)];
}


function processData(csv){

  let stratify = d3.stratify()
                  .id(d => d.emojis)
                  .parentId(d => d.group)(csv)

  stratify.sort((a,b) => (b.data.group > a.data.group) || b.data.n > a.data.n);
  return stratify;
}


export default Chord;

